@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;700&display=swap");
.dark-theme {
  --BG: hsl(230, 17%, 14%);
  --BGPattern: hsl(232, 19%, 15%);
  --CardBG: hsl(228, 28%, 20%);
  --DText: hsl(228, 34%, 66%);
  --Text: hsl(0, 0%, 100%);
}

.light-theme {
  --BG: hsl(0, 0%, 100%);
  --BGPattern: hsl(225, 100%, 98%);
  --CardBG: hsl(227, 47%, 96%);
  --DText: hsl(228, 12%, 44%);
  --Text: hsl(230, 17%, 14%);
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  outline: none;
  font-family: "Inter", sans-serif;
}

body {
  background: var(--BG);
}

.main {
  max-width: 1440px;
  margin: 0 auto;
  display: grid;
  width: 80%;
  grid-template-columns: 1fr;
  grid-template-rows: auto;
}

.main__header {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
}
.main__header .top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  color: var(--Text);
}

.bg {
  position: absolute;
  top: 0;
  background-color: var(--BGPattern);
  z-index: -110;
  width: 100%;
  height: 350px;
}